.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(255, 255, 255);
}

.App-link {
  color: #61dafb;
}

main{
  margin-top: 60px;
  margin-bottom: 45px;
}

button{
  padding: 0;
  border:0;
  background: none;
  font-size: 100%;
  font-family: inherit;
}
button:focus{
  outline: none;
}

